import React from 'react';

import { makeStyles, Chip } from '@material-ui/core';
import { Favorite } from '@material-ui/icons';
import clsx from 'clsx';
import {
  useRoute,
  useNavigation,
  useLongPress,
  RouteProp,
} from 'src/lib/hooks';

const useStyles = makeStyles((theme) => ({
  routeContainer: {
    display: 'flex',
  },
  root: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'start',
    alignItems: 'center',
    flexWrap: 'wrap',
  },
  chipRoot: {
    padding: '0px 0.6em',
    fontSize: '0.7rem',
    height: '1.3rem',
    margin: `0 ${theme.spacing(1 / 2)}px ${theme.spacing(1 / 2)}px 0`,
    [theme.breakpoints.up('sm')]: {
      maxWidth: 200,
    },
    '&.fav ': {
      '& .label': {
        paddingLeft: theme.spacing(0.5),
      },
    },
  },
  favoriteIcon: {
    width: '0.7rem',
    height: '0.7rem',
    margin: 0,
  },
}));

export type KuralChipItem = {
  label: string;
  navRoute: string;
  color: 'default' | 'primary' | 'secondary';
};
type RouteViewerProps = {
  className?: string;
  renderRight?: React.ReactNode;
};

type RouteChipProps = {
  route: RouteProp;
  onLongPress?: (route: RouteProp) => void;
};

const RouteChip: React.FC<RouteChipProps> = ({
  route,
  onLongPress,
}: RouteChipProps) => {
  const classes = useStyles();
  const [navigateTo] = useNavigation();
  const longPressEventHandlers = useLongPress(() => {
    onLongPress && onLongPress(route);
  });
  // TODO: longpress should not trigger click event
  return (
    <Chip
      {...(route.isFavourite
        ? { icon: <Favorite className={classes.favoriteIcon} /> }
        : {})}
      {...longPressEventHandlers}
      size='small'
      label={route.value}
      color={'primary'}
      onClick={(): void => {
        route.route && navigateTo(route.route)();
      }}
      style={{ backgroundColor: route.color }}
      classes={{
        root: `${classes.chipRoot} ${route.isFavourite && 'fav'}`,
        label: 'label',
      }}
    />
  );
};

const RouteViewer: React.FC<RouteViewerProps> = (props: RouteViewerProps) => {
  const classes = useStyles();
  const [, routes, toggleRouteFavourite] = useRoute({});
  return (
    <div className={clsx(classes.routeContainer, props.className)}>
      <div className={classes.root}>
        {routes.map((item, index) => (
          <RouteChip
            route={item}
            key={index}
            onLongPress={(route: RouteProp) => toggleRouteFavourite(route)}
          />
        ))}
      </div>
      {props.renderRight}
    </div>
  );
};

export default RouteViewer;
