import React from 'react';

import { makeStyles, ListItem, Chip, List } from '@material-ui/core';
import { Kural } from 'src/lib/api';
import { useKuralDetail } from 'src/lib/hooks';

import KuralCard from './KuralCard';

const useStyles = makeStyles((theme) => ({
  transliteratorList: {
    cursor: 'grab',
    userSelect: 'none',
    padding: theme.spacing(1),
    [theme.breakpoints.only('xs')]: {
      minHeight: 400,
    },
    '& li:not(:last-child)': {
      marginBottom: theme.spacing(1),
    },
  },
  kural: {
    padding: theme.spacing(1),
    borderBottom: 'unset',
    color: 'brown',
    fontWeight: 600,
    '& .kural-badge': {
      display: 'none',
    },
  },
  transliteration: {
    padding: 0,
    flexDirection: 'column',
    alignItems: 'self-end',

    '& .transliterator-name': {
      fontSize: '0.7em',
      height: '1.5em',
      backgroundColor: 'var(--app-secondary-color)',
      fontFamily: 'var(--app-font-family)',
      fontWeight: 'bold',
    },
    '& .content': {
      fontSize: '0.9em',
      paddingTop: theme.spacing(0.5),
      paddingBottom: theme.spacing(0.5),
      textAlign: 'justify',
      WebkitTextStrokeWidth: '0.2px',
      WebkitTextStroke: 'thin',
      letterSpacing: 1,
    },
  },
}));

type KuralDetailProps = {
  className?: string;
  kural: Kural | number;
};

const KuralDetail: React.FC<KuralDetailProps> = (props: KuralDetailProps) => {
  const classes = useStyles();
  const [kural, kuralDetail, transliterators] = useKuralDetail(props.kural);
  return (
    <>
      {kural && (
        <KuralCard
          kural={kural}
          className={classes.kural}
          componentType='div'></KuralCard>
      )}
      <List
        className={props.className}
        classes={{ root: classes.transliteratorList }}
        {...props}>
        {kuralDetail &&
          transliterators.map(({ id: transliteratorId, name }) => (
            <ListItem
              key={transliteratorId}
              className={classes.transliteration}>
              <Chip
                size='small'
                label={name}
                color={'primary'}
                className={'transliterator-name'}
              />
              <div className={'content'}>{kuralDetail[transliteratorId]}</div>
            </ListItem>
          ))}
      </List>
    </>
  );
};

export default KuralDetail;
