import React from 'react';

import { Container, makeStyles } from '@material-ui/core';
import { debug } from 'loglevel';
import { useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import {
  findCategoryRouteMapString,
  isCategoryValid,
  ITEMS_COUNT_MAP,
} from 'src/lib/api';
import { ROUTE_CONSTANTS } from 'src/routes';
import { AppState } from 'src/state';
import SwiperCore, { Virtual, Keyboard } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper.scss';
import './swiper-custom.scss';

import KuralDetail from './KuralDetail';
import RouteViewer from './RouteViewer';

SwiperCore.use([Virtual, Keyboard]);

type KuralDetailViewerProps = {
  className?: string;
};

const useStyles = makeStyles((theme) => ({
  routes: {
    padding: theme.spacing(1),
  },
}));

const KuralDetailViewer: React.FC<KuralDetailViewerProps> = (
  props: KuralDetailViewerProps
) => {
  const history = useHistory();
  const classes = useStyles();
  const INDEX_MAP = useSelector((st: AppState) => st.kuralIndexMap.index);
  const params = useParams<{ kural: string }>();
  const KURAL_NO = parseInt(params.kural);
  const [activeKuralNo, setActiveKuralNo] = React.useState(KURAL_NO);
  const slideKuralIds = Array.from(Array(ITEMS_COUNT_MAP.kural).keys()).map(
    (i) => i + 1
  );
  const renderSlide = (kId: number, index: number) => (
    <SwiperSlide key={kId} virtualIndex={index}>
      <KuralDetail kural={kId} />
    </SwiperSlide>
  );

  React.useEffect(() => {
    if (INDEX_MAP) {
      const newRoute = `${
        ROUTE_CONSTANTS.LIST
      }/${findCategoryRouteMapString(INDEX_MAP, { kural: activeKuralNo })}`;
      debug('replace route to => ', newRoute);
      history.replace(newRoute);
    }
  }, [INDEX_MAP, activeKuralNo, history]);

  if (!isCategoryValid('kural', KURAL_NO)) {
    return null;
  }
  return (
    <>
      <Container disableGutters className={classes.routes}>
        <RouteViewer />
      </Container>
      <Swiper
        autoHeight={true}
        keyboard={true}
        grabCursor={true}
        initialSlide={KURAL_NO - 1}
        spaceBetween={10}
        slidesPerView={1}
        allowSlideNext={
          slideKuralIds[slideKuralIds.length - 1] !== activeKuralNo
        }
        virtual
        onTransitionEnd={(swiper) => {
          setActiveKuralNo(swiper.activeIndex + 1);
        }}>
        {slideKuralIds.map((kId, index) => renderSlide(kId, index))}
      </Swiper>
    </>
  );
};

export default KuralDetailViewer;
